/*======================================
 ============== Default CSS =============
 ======================================== */

body {
  font-family: "IBM Plex Sans", sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}
section {
  padding: 100px 0;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
p {
  font-size: 15px;
  padding: 0;
  margin: 0;
}
.bold-500 {
  font-weight: 500;
}
a {
  font-size: 15px;
  color: #e61523 !important;
  text-decoration: none;
  transition: 0.5s all ease;
  -webkit-transition: 0.5s all ease;
  -moz-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
}
a:hover {
  color: red !important;
  text-decoration: none;
}
.oo {
  position: relative;
  z-index: 99;
}
.nav-hidden-items .form-control {
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #fff;
  box-shadow: none !important;
  padding-left: 0;
  padding-right: 0;
  width: 100px;
}
.nav-hidden-items .form-control:focus {
  border-bottom-color: #e61523 !important;
}
.nav-hidden-items {
  display: none;
}
.sticky {
  position: fixed !important;
  /* background: red !important; */
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  background: #fff !important;
  border-bottom: 1px solid #00000028 !important;
}
.sticky .nav-hidden-items {
  display: block !important;
}
.sticky .top-header-logo img {
  width: 150px !important;
}
input[type="text"] {
  font-size: 13px;
}
.top-header-leftarea .form-control {
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #fff;
  box-shadow: none !important;
  padding-left: 0;
  padding-right: 0;
}
.top-header-leftarea .form-control:focus {
  border-bottom-color: #e61523 !important;
}
.top-header-leftarea .form-control:focus::-webkit-input-placeholder {
  visibility: hidden;
}
.top-header-leftarea .form-control:focus:-ms-input-placeholder {
  visibility: hidden;
}
.top-header-leftarea .form-control:focus::placeholder {
  visibility: hidden;
}
.top-header-leftarea .form-control:hover::-webkit-input-placeholder {
  visibility: visible;
}
.top-header-leftarea .form-control:hover:-ms-input-placeholder {
  visibility: visible;
}
.top-header-leftarea .form-control:hover::placeholder {
  visibility: visible;
}
.top-header-logo img {
  width: 170px !important;
}
.top-header-rightarea a {
  font-size: 12px;
  font-weight: 500;
}
.top-header-rightarea i {
