  border-bottom: 1px solid #e8e8e8 !important;
  padding: 10px 0 !important;
}
.sidemenu-sublist .sidemenu-sublist-heading {
  text-transform: uppercase !important;
  font-size: 13px !important;
  color: #999 !important;
  font-weight: bold !important;
